import React, { useState } from 'react';
import * as FileSaver from 'file-saver';
import Papa from 'papaparse'
import './QuestionAnswer.css';
import { Grid, Button, TextField, Container } from '@mui/material';

function QuestionAnswer() {
  const [question, setQuestion] = useState('');
  const [answer, setAnswer] = useState('');

  const handleQuestionChange = (e) => {setQuestion(e.target.value);};

  const handleAnswerChange = (e) => {setAnswer(e.target.value);};

  const [textArea, setTextArea] = useState('');
  const handleSubmit = () => {
    if (question.trim() !== "" && answer.trim() !== "") {
        let questionToAdd = question;
        if (!question.endsWith("?")) {
            questionToAdd = question + "?";
        }
        setTextArea(`${textArea}\nQ:${questionToAdd}\nA:${answer}`);
        setQuestion('');
        setAnswer('');
    } else {
        alert("Please fill both fields before submitting");
    }
  };
//Variabile che mi permette di salvare il nome del file
  const [fileName, setFileName] = useState('');
  const handleFileNameChange = (e) => {setFileName(e.target.value);};

  //funzione che mi permette di scaricare il testo della textarea in formato JSON
  function handleDownloadJSONL() {
    if (!textArea || textArea.trim().length === 0) {
      alert("Non ci sono dati da scaricare in formato JSONL.");
      return;
    }
    let data = [];
    let questionRegex = /Q:(.*)/g;
    let answerRegex = /A:(.*)/g;
    let questions = textArea.match(questionRegex);
    let answers = textArea.match(answerRegex);

    if (!questions || !answers || questions.length !== answers.length) {
      alert("Errore nell'elaborazione dei dati, assicurati che ogni domanda sia seguita da una risposta");
      return;
    }
    for(let i = 0; i < questions.length; i++){
      data.push({prompt: questions[i].slice(2),completion: answers[i].slice(2)+'\n'});
    }
    let jsonl = data.map(item => JSON.stringify(item)).join("\n");
    let blob = new Blob([jsonl], { type: 'application/json' });
    FileSaver.saveAs(blob, `${fileName}.jsonl`);
  }

    //Funzione che mi permette di scaricare il testo della textarea in CSV
    function handleDownloadCSV(){
    if (!textArea || textArea.trim().length === 0) {
    alert("Non ci sono dati da scaricare in formato CSV.");
    return;
    }
    // crea un array di oggetti per ogni coppia domanda-risposta
    let data = [];
    let questionRegex = /Q:(.*)/g;
    let answerRegex = /A:(.*)/g;
    let questions = textArea.match(questionRegex);
let answers = textArea.match(answerRegex);

if (!questions || !answers || questions.length !== answers.length) {
    alert("Errore nell'elaborazione dei dati, assicurati che ogni domanda sia seguita da una risposta");
    return;
}

for(let i = 0; i < questions.length; i++){
    data.push({
        prompt: questions[i].slice(2),
        completion: answers[i].slice(2)
    });
}
let csv = Papa.unparse(data);
// salva il file csv
let blob = new Blob([csv], { type: 'text/csv;charset=utf-8' });
      FileSaver.saveAs(blob, `${fileName}.csv`);
    }
    return (
      <div>
      <Grid Container>
      <Grid item xs={12} md={6}>
      <label>Question:</label>
      <TextField
               label="Inserisci testo per la domanda"
               variant="filled"
               fullWidth
               value={question}
               onChange={handleQuestionChange}
           />
      </Grid>
      </Grid>
      <label>Answer:</label>
      <TextField
               label="Answer"
               variant="filled"
               fullWidth
               value={answer}
               onChange={handleAnswerChange}  
           />
      <br />
      <textarea readOnly value={textArea} style={{width:700, height:300}}/>
      <Grid container alignItems="center" justify="center">
      <Grid item xs={12}>
      <Button id='submit'variant="contained" color="primary" onClick={handleSubmit}>
      Submit
    </Button>
    <br/>
    <TextField label="Nome del file" variant="filled" fullWidth value={fileName} onChange={handleFileNameChange}/>
    <Button id="json" variant="contained" color="primary" onClick={()=>handleDownloadJSONL()}>
      Download Json
    </Button>
    <Button id="csv" variant="contained" color="primary" onClick={()=>handleDownloadCSV('csv')}>
     Download CSV
     </Button>
  </Grid>
</Grid>

</div>
);
}

export default QuestionAnswer;
